form {
  font-size: 16px;
}

.home {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: clamp(2.5rem,(5vw+0.5rem),4rem);
  text-align: center;
  min-height: calc(100vh - 50px);
  margin-top: -60px;
  background-image:url(/static/media/bg.5128c200.jpg);
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: cover;
}

.home a{
  display: inline-block;
  font-size: 16px;
  padding: 15px;
  background-color: #e1e7c7ff;
  background-color: var(--cold-green);
  color: #4c566a;
  color: var(--dark-5);
  text-decoration: none;
  border-radius: 999px;
  /* box-shadow: 0px 6px 10px rgba(59, 59, 59, 0.92); */
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 
  rgba(0,0,0,0.19);
  transition: all 0.3s ease-in-out;
  font-weight: 800;
}

.home a:hover{
  transform: translateY(-5px);
  /* box-shadow: 0px 8px 20px rgba(68, 68, 70, 0.849); */
  box-shadow: 0 12px 
  16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
}

.home .text{
  margin-top: -50px;
  color: #3b4252;
  color: var(--dark-2);
}

.home p{
  color: #434c5e;
  color: var(--dark-4);
  margin-top: 10px;
  font-weight: 400;
  margin-bottom: 20px;
  font-size: clamp(2.2rem,(4vw+0.5rem),3rem);
  text-shadow: 0px 4px 10px rgba(83, 83, 83, 0.76);
}

.org{
  font-size: 16px;
}

.org h1{
  margin-top: 60px;
  margin-bottom: 30px;
  font-size: 40px;
}



.form-control {
  font-size: 16px;
}

.about{
  font-size: 16px;
}

.about h2{
  margin-top: 30px;
  margin-bottom: 15px;
}

.about p, .about li{
margin-bottom: 10px;
}

.about li{
  margin-left: 15px;
}
.timeline {
  position: relative;
  max-width: 1000px;
  margin: 0 auto;
  font-size: 12px;
}

.timeline_content{
  box-shadow: 0 5px 50px 0 rgba(0,0,0,0.19);
}

.timeline_content h2{
  margin-bottom: 10px;
}

/* The actual timeline (the vertical ruler) */
.timeline::after {
  content: "";
  position: absolute;
  width: 6px;
  background-color: #93c2dbff;
  top: 18px;
  bottom: -18px;
  left: 50%;
  margin-left: -3px;
}

.timeline:last-child::after{
  content: "";
  height: 0;
}

/* Container around content */
.timeline_container {
  padding: 10px 40px;
  position: relative;
  background-color: inherit;
  width: 50%;
}

/* The circles on the timeline */
.timeline_container::after {
  content: "";
  position: absolute;
  width: 25px;
  height: 25px;
  right: -13px;
  background-color: #bdd9d0ff;
  border: 4px solid #93c2dbff;
  top: 15px;
  border-radius: 50%;
  z-index: 1;
}

/* Place the container to the left */
.timeline .left {
  left: 0;
}

/* Place the container to the right */
.timeline .right {
  left: 50%;
}

/* Add arrows to the left container (pointing right) */
.timeline .left::before {
  content: " ";
  height: 0;
  position: absolute;
  top: 22px;
  width: 0;
  z-index: 1;
  right: 30px;
  border: medium solid #f7f7f7;
  border-width: 10px 0 10px 10px;
  border-color: transparent transparent transparent #f7f7f7;
}

/* Add arrows to the right container (pointing left) */
.timeline .right::before {
  content: " ";
  height: 0;
  position: absolute;
  top: 22px;
  width: 0;
  z-index: 1;
  left: 30px;
  border: medium solid #f7f7f7;
  border-width: 10px 10px 10px 0;
  border-color: transparent #f7f7f7 transparent transparent;
}

/* Fix the circle for containers on the right side */
.timeline .right::after {
  left: -13px;
}

/* The actual content */
.timeline_content {
  padding: 20px 30px;
  background-color: #f7f7f7;
  position: relative;
  border-radius: 6px;
}

/* Media queries - Responsive timeline on screens less than 600px wide */
@media screen and (max-width: 600px) {
  /* Place the timelime to the left */
  .timeline::after {
    left: 31px;
  }

  /* Full-width containers */
  .timeline_container {
    width: 100%;
    padding-left: 70px;
    padding-right: 25px;
  }

  /* Make sure that all arrows are pointing leftwards */
  .timeline .left::before, .timeline .right::before  {
    left: 60px;
    border: medium solid #f7f7f7;
    border-width: 10px 10px 10px 0;
    border-color: transparent #f7f7f7 transparent transparent;
  }

  /* Make sure all circles are at the same spot */
  .timeline .left::after,
  .timeline .right::after {
    left: 18px;
  }

  /* Make all right containers behave like the left ones */
  .timeline .right {
    left: 0%;
  }
}


